body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  overflow: hidden;
  z-index: 1;
}

td, th, .ff-fix {
  cursor: pointer;
  padding: 10px;
  position: relative;
}

td:hover::after,
.ff-fix:hover::after {
  background-color: #ffa;
  content: '\A0';
  height: 10000px;
  left: 0;
  position: absolute;
  top: -5000px;
  width: 100%;
  z-index: -1;
}

.btn {
  border-radius: 0;
}

.player_1 {
  background-color: #99dfd2;
}

.player_2 {
  background-color: #ffbd75;
}

.App {
  text-align: center;
  color: #727272;
}

.App-logo {
  -webkit-animation: App-logo-spin 5s linear;
          animation: App-logo-spin 5s linear;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  height: 80px;
}

.App-header {
  background-color: #fcffb7;
  padding: 20px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  0% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
  100%  { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }
}

@keyframes App-logo-spin {
  0% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
  100%  { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }
}

